'use client';

import React from 'react';
import AppLayout from '@/components/AppLayout';
import { useRouter } from 'next/navigation';

// main wallet landing page — it routes to the wallet subpages
export default function VendorWalletPage() {
  const router = useRouter();

  const walletStats = {
    withdrawable: 0.0,
    available: 0.0,
    totalEarnings: 0.0,
    totalWithdrawals: 0,
    thisMonth: 0.0
  };

  return (
    <AppLayout showBottomNav={true} userRole="vendor">
      <div className="min-h-screen bg-white">
        <div className="p-4">
          <h1 className="text-xl font-bold text-gray-900 mb-2">Wallet</h1>
          <p className="text-sm text-gray-600 mb-6">Manage your earnings and withdrawals</p>

          {/* Withdrawable Amount Card */}
          <div className="bg-system-blue-light rounded-lg p-6 mb-6">
            <p className="text-white text-sm mb-2">Withdrawable Amount</p>
            <p className="text-white text-4xl font-bold mb-4">₦{walletStats.withdrawable.toFixed(2)}</p>
            <button
              onClick={() => router.push('/vendor/wallet/withdraw')}
              className="flex items-center justify-center gap-2 w-full py-3 bg-white text-system-blue-light rounded-full font-medium hover:bg-gray-50 transition-colors"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
              Withdraw Earnings
            </button>
          </div>

          {/* Overview Stats */}
          <div>
            <h2 className="text-base font-semibold text-gray-900 mb-4">Overview</h2>
            <div className="grid grid-cols-2 gap-3">
              <div className="bg-green-50 rounded-lg p-4">
                <p className="text-xs text-gray-600 mb-1">Available Balance</p>
                <p className="text-xl font-bold text-gray-900">₦{walletStats.available.toFixed(2)}</p>
              </div>

              <div className="bg-purple-50 rounded-lg p-4">
                <p className="text-xs text-gray-600 mb-1">Total Earnings</p>
                <p className="text-xl font-bold text-gray-900">₦{walletStats.totalEarnings.toFixed(2)}</p>
              </div>

              <div className="bg-blue-50 rounded-lg p-4">
                <p className="text-xs text-gray-600 mb-1">Total Withdrawals</p>
                <p className="text-xl font-bold text-gray-900">{walletStats.totalWithdrawals}</p>
              </div>

              <div className="bg-purple-50 rounded-lg p-4">
                <p className="text-xs text-gray-600 mb-1">This Month</p>
                <p className="text-xl font-bold text-gray-900">₦{walletStats.thisMonth.toFixed(2)}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </AppLayout>
  );
}